.explore-view {
  display: flex;
  height: 100%;
  overflow: hidden; }

.sidebar {
  height: calc(100vh - 50px); }

.main-content {
  overflow: auto;
  max-height: calc(100vh - 50px);
  width: 100%;

  .content-top-bar {
    display: flex;
    color: #575449;
    align-items: center;
    width: 100%;
    padding: 30px;
    .results-count {
      font-size: 16px; }
    .results-view-type {
      margin-left: auto;
      display: flex;
      .view-type {
        width: 24px;
        height: 24px;
        cursor: pointer;
        margin: 5px;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: contain; } }
    .results-seach {
        margin-left: 10px;
        input {
          border-radius: 20px;
          padding: 2px 10px;
          outline: none; } } }

  .content-filters-summary {
    display: flex;
    margin-bottom: 15px;
    padding: 0px 30px;
    .filter-recap-item {
      cursor: pointer;
      font-size: 16px;
      line-height: 16px;
      border-radius: 12px;
      background: #F3EFE6;
      padding: 4px 8px;
      margin-right: 6px; } } }
