.App {
  text-align: left;
  max-height: 100vh;
  height: 100vh;
  overflow: hidden;
  background-color: #FBFAF7; }

.app-content {
  max-height: calc(100vh - 50px);
  height: 100%; }

.loading {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column; }
